enum Role {
  STUDENT = 'Student',
  Parent = "Parent",
  Security = "Security",
  LibraryStaff = "LibraryStaff",
  KitchenStaff = "KitchenStaff",
  SUBJECTTEACHER = 'SubjectTeacher',
  TEACHER = 'Teacher',
  GroupLeader = "GroupLeader",
  EDITOR = 'Editor',
  MANAGER = 'Manager',
  ADMINISTRATOR = 'Administrator',
  HOST = 'Host',
}

export const Roles = [
  Role.STUDENT,
  Role.Parent,
  Role.TEACHER,
  Role.SUBJECTTEACHER,
  Role.GroupLeader,
  Role.LibraryStaff,
  Role.KitchenStaff,
  Role.Security,
  Role.EDITOR,
  Role.MANAGER,
  Role.ADMINISTRATOR,
  Role.HOST];

export default Role;

export const getRoleName = (role: Role) => {
  switch (role) {
    case Role.HOST:
      return "Qun tr H thng";
    case Role.ADMINISTRATOR:
      return "Qun tr Nh trng";
    case Role.TEACHER:
      return "Gio vin ch nhim";
    case Role.SUBJECTTEACHER:
      return "Gio vin b mn";
    case Role.Parent:
      return "Ph huynh";
    case Role.STUDENT:
      return "Hc sinh";
    case Role.GroupLeader:
      return "T trng b mn";
    case Role.EDITOR:
      return "Vn th";
    case Role.MANAGER:
      return "Ban gim hiu";
    case Role.LibraryStaff:
      return "NV th vin";
    case Role.KitchenStaff:
      return "NV nh bp";
    case Role.Security:
      return "Bo v";
  }
}